.em-ribbon-wrapper {
  position: fixed;
  pointer-events: none;
  z-index: 999999;
}
.em-ribbon-wrapper .em-ribbon {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  font-family: "HelveticaNeue-CondensedBold", "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  position: relative;
  text-align: center;
  font-size: 14px;
  width: 100%;
  box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.3);
  pointer-events: none;
  opacity: 0.7;
  overflow: hidden;
}
.em-ribbon-wrapper .em-ribbon .em-ribbon-label {
  max-width: 80%;
}
.em-ribbon-wrapper .em-top-left-ribbon,
.em-ribbon-wrapper .em-bottom-right-ribbon {
  transform: rotate(-45deg);
}
.em-ribbon-wrapper .em-top-right-ribbon,
.em-ribbon-wrapper .em-bottom-left-ribbon {
  transform: rotate(45deg);
}
.em-ribbon-wrapper .em-extra-small-ribbon {
  height: 16px;
}
.em-ribbon-wrapper .em-extra-small-ribbon.em-top-left-ribbon {
  left: -20%;
  top: 50px;
}
.em-ribbon-wrapper .em-extra-small-ribbon.em-top-right-ribbon {
  left: 20%;
  top: 50px;
}
.em-ribbon-wrapper .em-extra-small-ribbon.em-bottom-left-ribbon {
  bottom: 50px;
  left: -20%;
}
.em-ribbon-wrapper .em-extra-small-ribbon.em-bottom-right-ribbon {
  bottom: 50px;
  left: 20%;
}
.em-ribbon-wrapper .em-small-ribbon {
  height: 20px;
}
.em-ribbon-wrapper .em-small-ribbon.em-top-left-ribbon {
  left: -20%;
  top: 50px;
}
.em-ribbon-wrapper .em-small-ribbon.em-top-right-ribbon {
  left: 20%;
  top: 50px;
}
.em-ribbon-wrapper .em-small-ribbon.em-bottom-left-ribbon {
  bottom: 50px;
  left: -20%;
}
.em-ribbon-wrapper .em-small-ribbon.em-bottom-right-ribbon {
  bottom: 50px;
  left: 20%;
}
.em-ribbon-wrapper .em-normal-ribbon {
  height: 24px;
}
.em-ribbon-wrapper .em-normal-ribbon.em-top-left-ribbon {
  left: -20%;
  top: 50px;
}
.em-ribbon-wrapper .em-normal-ribbon.em-top-right-ribbon {
  left: 20%;
  top: 50px;
}
.em-ribbon-wrapper .em-normal-ribbon.em-bottom-left-ribbon {
  bottom: 50px;
  left: -20%;
}
.em-ribbon-wrapper .em-normal-ribbon.em-bottom-right-ribbon {
  bottom: 50px;
  left: 20%;
}
.em-ribbon-wrapper .em-large-ribbon {
  height: 30px;
}
.em-ribbon-wrapper .em-large-ribbon.em-top-left-ribbon {
  left: -20%;
  top: 45px;
}
.em-ribbon-wrapper .em-large-ribbon.em-top-right-ribbon {
  left: 20%;
  top: 45px;
}
.em-ribbon-wrapper .em-large-ribbon.em-bottom-left-ribbon {
  left: -20%;
  bottom: 45px;
}
.em-ribbon-wrapper .em-large-ribbon.em-bottom-right-ribbon {
  left: 20%;
  bottom: 45px;
}
.em-ribbon-wrapper .em-extra-large-ribbon {
  height: 36px;
}
.em-ribbon-wrapper .em-extra-large-ribbon.em-top-left-ribbon {
  left: -21%;
  top: 40px;
}
.em-ribbon-wrapper .em-extra-large-ribbon.em-top-right-ribbon {
  left: 21%;
  top: 40px;
}
.em-ribbon-wrapper .em-extra-large-ribbon.em-bottom-left-ribbon {
  left: -21%;
  bottom: 40px;
}
.em-ribbon-wrapper .em-extra-large-ribbon.em-bottom-right-ribbon {
  left: 21%;
  bottom: 40px;
}

.em-extra-small-ribbon-wrapper,
.em-small-ribbon-wrapper,
.em-normal-ribbon-wrapper,
.em-large-ribbon-wrapper,
.em-extra-large-ribbon-wrapper {
  width: 200px;
}

.em-top-left-wrapper {
  top: 0;
  left: 0;
}

.em-top-right-wrapper {
  top: 0;
  right: 0;
}

.em-bottom-left-wrapper {
  bottom: 0;
  left: 0;
}

.em-bottom-right-wrapper {
  bottom: 0;
  right: 0;
}